var Popup = {
	_target: null,
	_defaults: {
		width: '99%',
		height: '99%',
		onLoad: function() {
			console.log(this);
			//var url = this.href.split("?")[0];
			//var query_string = $.query.load(this.href).set('type', 'iframe').toString();
			//
			//this.href = url + query_string;
			//var title = this.element.data('title');
			//if (title !== false) {
			//	this.title = title ? title : this.element.html();
			//}
			//
			//Popup._target = this.element;
		}
	},
	html: function(html, params) {
		if(!params) params = {};

		if(html instanceof jQuery)
			var options = {
				inline: true,
				href: html
			};
		else
			var options = {
				html: html
			};

		return this.get($.extend(params, options));
	},
	iframe: function(href, params) {
		if(!params) params = {};

		var options = {
			iframe: true
		};

		return this.href(href, $.extend(params, options));
	},
	href: function(href, params) {
		if(!params) params = {};

		var options = {
			href: href
		};

		return this.get($.extend(params, options));
	},
	close: function() {
		$.colorbox.close();
	},
	get: function(options) {
		var settings = $.extend(this._defaults, options);

		return $.colorbox(settings);
	},
	onOpen: function() {

	}
}
